.footer {
    box-sizing: border-box;
    width: 100%;
    padding: rem(68) 0 rem(74);
    color: #fff;
    background: #0b2530;

    @media (max-width: 991px) {
        padding: rem(42) 0 rem(62);
    }

    @media (max-width: 574px) {
        padding: rem(26) 0 rem(62);
    }

    &__top {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__col {
        @include size-lg(4);
        @include size-md(6);
        @include size-xs(12);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: rem(70);


        @media (max-width: 574px) {
            margin-bottom: rem(60);
        }

        &:first-child {
            @include size-lg(4);
            @include size-md(12);
            @include size-xs(12);
        }
    }

    &__logo {
        display: block;
        margin-bottom: rem(20);

        @media (max-width: 991px) {
            margin-bottom: rem(35);
        }
    }

    &__menu {

        @media (max-width: 991px) {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
        }

        &-item {

            @media (max-width: 991px) {
                margin-right: rem(16);
                margin-bottom: rem(16);
            }
            &:not(:last-child) {
                margin-bottom: rem(16);
            }
        }

        &-link {
            font-size: rem(14);
            font-family: "Neometric Alt", sans-serif;
            font-weight: 600;
            line-height: 1.4;
            color: #fff;
        }
    }

    &__title, &__phone {
        display: block;
        font-size: rem(28);
        font-family: "Neometric Alt", sans-serif;
        font-weight: 600;
        line-height: 109%;
        color: #fff;

        @media (max-width: 574px) {
            font-size: rem(20);
        }

        &:not(:last-child) {
            margin-bottom: rem(20);
        }

        &-fillial {
            color: #29a4dd;
        }
    }

    &__address, &__phone-text {
        line-height: 2;

        @media (max-width: 991px) {
            min-height: rem(75);
            margin-bottom: rem(48);
        }
    }

    &__info {

        @media (max-width: 991px) {
            margin-bottom: rem(48);
        }

        &-item {
            display: flex;
            align-items: center;
            margin-bottom: rem(12);
            line-height: 1.4;

            span {
                display: inline-block;
                margin-right: rem(8);
                background-size: contain;
                background-repeat: no-repeat;

                &.ic-clock {
                    width: rem(16);
                    height: rem(16);
                    background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='8' cy='8' r='7.5' stroke='%232E8FBC'/%3E%3Cpath d='M8 4V9.6L11.2 10.8' stroke='%232E8FBC'/%3E%3C/svg%3E");
                }

                &.ic-mail {
                    width: rem(16);
                    height: rem(11);
                    background-image: url("data:image/svg+xml,%3Csvg width='18' height='13' viewBox='0 0 18 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1V12H17V1M1 1H17M1 1L8.74194 7.36842L17 1' stroke='%232E8FBC'/%3E%3C/svg%3E");
                }
            }

            a {
                color: #fff;
            }
        }
    }

    &__social {
        display: flex;
        align-items: center;

        @media (max-width: 991px) {
            margin-bottom: rem(48);
        }

        &-item {
            &:not(:last-child) {
                margin-right: rem(16);
            }
        }

