    &__apps {
        display: flex;
        align-items: center;

        &-item {
            &:not(:last-child) {
                margin-right: rem(8);
            }
        }
    }

    &__bottom {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        justify-content: space-between;
        line-height: 1.3;
        color: #666e72;

        &-col {
            @include size-lg(4);
            @include size-md(6);
            @include size-xs(12);
        }
    }

    &__copyrite {
        @media (max-width: 767px) {
            margin-bottom: rem(60);
        }
    }

    &__legal-text {
        color: #666e72;
    }
}